import React, { Component } from 'react';
import Home from './views/Home';
import styled from 'styled-components';
import Header from './blocks/Header';
import Navigator from './blocks/Navigator';

const Title = styled.div`
    font-size: 40px;
    display: flex;
    background-color: orange;
    flex: 2 2 auto;
    align-items: center;
    justify-content: center;
`;

const Icon = styled.div`
    font-size: 25px;
    display: flex;
    background-color: green;
    align-items: center;
    flex: 1 1 auto;
    justify-content: center;
`;

const App = () => {
    const testLinks = [
        {
          title: 'Foo',
          link: 'http://google.com/'
        },
        {
          title: 'Bar',
          link: 'http://bing.com/'
        },
        {
          title: 'Baz',
          link: 'http://googleearth.com/'
        }
    ];
    return (
        <div>
            <Header>
                <Icon>
                    (-_-)
                </Icon>
                <Title>
                    TITLE
                </Title>
                <Navigator links={testLinks} />
            </Header>
            <Home />
        </div>
    );
}

export default App;



